<template>
	<view class="container">
		<!-- Á≠õÈÄâÊ†è -->
		<view class="filter-bar">
			<picker :range="statusFilters" range-key="label" @change="statusChange">
				<view class="filter-item">
					<text>{{ currentStatus.label }}</text>
					<text class="arrow">‚ñº</text>
				</view>
			</picker>
			
			<picker :range="typeFilters" range-key="label" @change="typeChange">
				<view class="filter-item">
					<text>{{ currentType.label }}</text>
					<text class="arrow">‚ñº</text>
				</view>
			</picker>
		</view>
		
		<!-- ÂéÜÂè≤ÂàóË°® -->
		<view class="history-list">
			<view 
				v-for="(item, index) in filteredHistory" 
				:key="index"
				class="history-item"
				@click="showDetail(item)"
			>
				<view class="item-left">
					<view class="item-icon">{{ getTypeIcon(item.type) }}</view>
					<view class="item-info">
						<text class="item-title">{{ getTypeName(item.type) }}</text>
						<text class="item-printer">{{ item.printer }}</text>
						<text class="item-time">{{ formatTime(item.time) }}</text>
					</view>
				</view>
				
				<view class="item-right">
					<view class="item-status" :class="item.status">
						{{ getStatusText(item.status) }}
					</view>
					<text class="arrow">></text>
				</view>
			</view>
			
			<view class="empty-tip" v-if="filteredHistory.length === 0">
				<text>ÊöÇÊó†ÊâìÂç∞ËÆ∞ÂΩï</text>
			</view>
		</view>
		
		<!-- Âä†ËΩΩÊõ¥Â§ö -->
		<view class="load-more" v-if="hasMore">
			<button class="load-btn" @click="loadMore">Âä†ËΩΩÊõ¥Â§ö</button>
		</view>
		
		<!-- ËØ¶ÊÉÖÂØπËØùÊ°Ü -->
		<view class="dialog-mask" v-if="showDetailDialog" @click="closeDetail">
			<view class="dialog" @click.stop>
				<view class="dialog-title">ÊâìÂç∞ËØ¶ÊÉÖ</view>
				
				<view class="detail-content">
					<view class="detail-item">
						<text class="detail-label">‰ªªÂä°ID</text>
						<text class="detail-value">{{ currentDetail.id }}</text>
					</view>
					<view class="detail-item">
						<text class="detail-label">ÊâìÂç∞Á±ªÂûã</text>
						<text class="detail-value">{{ getTypeName(currentDetail.type) }}</text>
					</view>
					<view class="detail-item">
						<text class="detail-label">ÊâìÂç∞Êú∫</text>
						<text class="detail-value">{{ currentDetail.printer }}</text>
					</view>
					<view class="detail-item">
						<text class="detail-label">ÊâìÂç∞Êó∂Èó¥</text>
						<text class="detail-value">{{ formatFullTime(currentDetail.time) }}</text>
					</view>
					<view class="detail-item">
						<text class="detail-label">Áä∂ÊÄÅ</text>
						<text class="detail-value" :class="'status-' + currentDetail.status">
							{{ getStatusText(currentDetail.status) }}
						</text>
					</view>
				</view>
				
				<view class="dialog-footer">
					<button class="close-btn" @click="closeDetail">ÂÖ≥Èó≠</button>
					<button class="reprint-btn" @click="reprintJob" v-if="currentDetail.status === 'failed'">
						ÈáçÊñ∞ÊâìÂç∞
					</button>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import printApi from '@/utils/printApi.js';
	
	export default {
		data() {
			return {
				historyList: [],
				filteredHistory: [],
				currentStatus: { label: 'ÂÖ®ÈÉ®Áä∂ÊÄÅ', value: 'all' },
				currentType: { label: 'ÂÖ®ÈÉ®Á±ªÂûã', value: 'all' },
				statusFilters: [
					{ label: 'ÂÖ®ÈÉ®Áä∂ÊÄÅ', value: 'all' },
					{ label: 'ÊàêÂäü', value: 'success' },
					{ label: 'Â§±Ë¥•', value: 'failed' },
					{ label: 'ËøõË°å‰∏≠', value: 'pending' }
				],
				typeFilters: [
					{ label: 'ÂÖ®ÈÉ®Á±ªÂûã', value: 'all' },
					{ label: 'ÊñáÊú¨ÊâìÂç∞', value: 'text' },
					{ label: 'ÂõæÁâáÊâìÂç∞', value: 'image' },
					{ label: 'ÊñáÊ°£ÊâìÂç∞', value: 'document' },
					{ label: 'Ê†áÁ≠æÊâìÂç∞', value: 'label' }
				],
				currentPage: 1,
				pageSize: 20,
				hasMore: false,
				showDetailDialog: false,
				currentDetail: {}
			}
		},
		
		onLoad() {
			this.loadHistory();
		},
		
		onShow() {
			// ÊØèÊ¨°ÊòæÁ§∫Êó∂Âà∑Êñ∞ÂéÜÂè≤ËÆ∞ÂΩï
			this.loadHistory();
		},
		
		methods: {
			// Âä†ËΩΩÂéÜÂè≤ËÆ∞ÂΩï
			loadHistory() {
				const history = uni.getStorageSync('printHistory') || [];
				this.historyList = history;
				this.filterHistory();
			},
			
			// Á≠õÈÄâÂéÜÂè≤ËÆ∞ÂΩï
			filterHistory() {
				let filtered = this.historyList;
				
				// ÊåâÁä∂ÊÄÅÁ≠õÈÄâ
				if (this.currentStatus.value !== 'all') {
					filtered = filtered.filter(item => item.status === this.currentStatus.value);
				}
				
				// ÊåâÁ±ªÂûãÁ≠õÈÄâ
				if (this.currentType.value !== 'all') {
					filtered = filtered.filter(item => item.type === this.currentType.value);
				}
				
				// ÂàÜÈ°µ
				const start = 0;
				const end = this.currentPage * this.pageSize;
				this.filteredHistory = filtered.slice(start, end);
				this.hasMore = filtered.length > end;
			},
			
			// Áä∂ÊÄÅÂèòÂåñ
			statusChange(e) {
				this.currentStatus = this.statusFilters[e.detail.value];
				this.currentPage = 1;
				this.filterHistory();
			},
			
			// Á±ªÂûãÂèòÂåñ
			typeChange(e) {
				this.currentType = this.typeFilters[e.detail.value];
				this.currentPage = 1;
				this.filterHistory();
			},
			
			// Âä†ËΩΩÊõ¥Â§ö
			loadMore() {
				this.currentPage++;
				this.filterHistory();
			},
			
			// ÊòæÁ§∫ËØ¶ÊÉÖ
			showDetail(item) {
				this.currentDetail = item;
				this.showDetailDialog = true;
			},
			
			// ÂÖ≥Èó≠ËØ¶ÊÉÖ
			closeDetail() {
				this.showDetailDialog = false;
			},
			
			// ÈáçÊñ∞ÊâìÂç∞
			reprintJob() {
				uni.showModal({
					title: 'ÊèêÁ§∫',
					content: 'Á°ÆÂÆöË¶ÅÈáçÊñ∞ÊâìÂç∞ÂêóÔºü',
					success: (res) => {
						if (res.confirm) {
							// Ë∑≥ËΩ¨Âà∞ÊâìÂç∞È°µÈù¢ÔºåÂπ∂‰º†ÈÄíÂèÇÊï∞
							uni.navigateTo({
								url: '/pages/print/print?type=' + this.currentDetail.type
							});
							this.closeDetail();
						}
					}
				});
			},
			
			// Ëé∑ÂèñÁ±ªÂûãÂõæÊ†á
			getTypeIcon(type) {
				const iconMap = {
					text: 'üìÑ',
					image: 'üñºÔ∏è',
					document: 'üìã',
					label: 'üè∑Ô∏è'
				};
				return iconMap[type] || 'üìÑ';
			},
			
			// Ëé∑ÂèñÁ±ªÂûãÂêçÁß∞
			getTypeName(type) {
				const nameMap = {
					text: 'ÊñáÊú¨ÊâìÂç∞',
					image: 'ÂõæÁâáÊâìÂç∞',
					document: 'ÊñáÊ°£ÊâìÂç∞',
					label: 'Ê†áÁ≠æÊâìÂç∞'
				};
				return nameMap[type] || 'Êú™Áü•';
			},
			
			// Ëé∑ÂèñÁä∂ÊÄÅÊñáÊú¨
			getStatusText(status) {
				const textMap = {
					success: 'ÊàêÂäü',
					failed: 'Â§±Ë¥•',
					pending: 'ËøõË°å‰∏≠'
				};
				return textMap[status] || 'Êú™Áü•';
			},
			
			// Ê†ºÂºèÂåñÊó∂Èó¥
			formatTime(time) {
				const date = new Date(time);
				const now = new Date();
				const diff = now - date;
				
				// Â∞è‰∫é1ÂàÜÈíü
				if (diff < 60000) {
					return 'ÂàöÂàö';
				}
				
				// Â∞è‰∫é1Â∞èÊó∂
				if (diff < 3600000) {
					return Math.floor(diff / 60000) + 'ÂàÜÈíüÂâç';
				}
				
				// Â∞è‰∫é1Â§©
				if (diff < 86400000) {
					return Math.floor(diff / 3600000) + 'Â∞èÊó∂Ââç';
				}
				
				// Â∞è‰∫é7Â§©
				if (diff < 604800000) {
					return Math.floor(diff / 86400000) + 'Â§©Ââç';
				}
				
				// Ë∂ÖËøá7Â§©ÊòæÁ§∫ÂÖ∑‰ΩìÊó•Êúü
				const month = date.getMonth() + 1;
				const day = date.getDate();
				return `${month}Êúà${day}Êó•`;
			},
			
			// Ê†ºÂºèÂåñÂÆåÊï¥Êó∂Èó¥
			formatFullTime(time) {
				const date = new Date(time);
				const year = date.getFullYear();
				const month = String(date.getMonth() + 1).padStart(2, '0');
				const day = String(date.getDate()).padStart(2, '0');
				const hour = String(date.getHours()).padStart(2, '0');
				const minute = String(date.getMinutes()).padStart(2, '0');
				const second = String(date.getSeconds()).padStart(2, '0');
				return `${year}-${month}-${day} ${hour}:${minute}:${second}`;
			}
		}
	}
</script>

